.card {
    width: 250px;
    height: 100%;
    box-shadow: 0px 0px 15px 1px #efefef;
    border-radius: 1rem;

    & .card-image {
        display: flex;

        & .image {
            display: inline-block;
            border-radius: 1rem 1rem 0 0;
            background-clip: initial;
            width: 100%;
            min-height: 150px;
            overflow: hidden;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
        }

        & .rating {
            position: absolute;
            padding: .3rem .5rem;
            color: white;
            font-weight: bold;
            font-size: .8rem;
            margin: 1rem 0;
            background-color: $primary;
            border-radius: 0 1rem 1rem 0;

            & .icon svg {
                width: .7rem;
                height: .7rem;

            }
        }
    }

    & .card-title {
        font-size: 1.2rem;
        font-weight: 500;
        text-align: center;
        padding: 0 1rem;
    }

    & .card-subtitle {
        display: block;
        font-size: .8rem;
        font-weight: 500;
        text-align: left;
        padding: .2rem 1rem;
    }

    & .card-description {
        font-size: .9rem;
        padding: 0 1rem;
    }

    & .card-footer {
        display: flex;
        justify-content: center;
        font-size: 1rem;
        padding: 1rem 2.5rem;
        height: max-content;

        & a {
            padding: .65rem 1.5rem;
        }
    }
}

@media (min-width: 320px) {
    .grid {
        justify-content: center;
        align-items: center;
    }
}

@media (min-width: 600px) {
    .grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 800px) {
    .grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 1100px) {
    .grid {
        grid-template-columns: repeat(4, 1fr);
    }
}