.banner {
    z-index: 999;

    & .hero-image {
        background-position: center;
        background-repeat: no-repeat;
        background-clip: border-box;
        background-size: cover;
        width: 100%;
        // height: 100vh;
    }

    & .hero-container {
        display: flex;
        justify-content: center;
        padding: 5rem;
        position: absolute;
        width: 100%;
        top: 10%;
        left: 0;
        right: 0;
    }

    & .hero-content {
        border-radius: 1rem;
        // background-color: rgba(#000, $alpha: 0.3);
        display: flex;
        justify-content: center;
        flex-direction: column;
        flex-wrap: nowrap;
        align-content: center;
        align-items: center;
        padding: 2rem;
        color: white;
        max-width: 28rem;

        & a {
            margin: 1rem 0;
            font-size: large;
            border-radius: & .50rem;
        }

        & .title {
            font-size: 2rem;
            text-align: center;
            text-shadow: 1px 1px rgba(0, 0, 0, 0.5);

            & svg {
                fill: #fff;
                width: 2rem;
                height: 2rem;
            }
        }

        & .description {
            font-size: 1rem;
            text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
            text-align: center;
        }
    }

}

@media only screen and (max-width:520px) {
    .banner {
        & .hero-image {
            width: 100%;
            // height: 100vh;
        }

        & .hero-container {
            padding: 0;
            width: 100%;
            top: 20%;
            left: 0;
            right: 0;
        }

        & .hero-content {
            padding: 1rem;
            max-width: 100%;

            & a {
                margin: 1rem 0;
                font-size: medium;
                border-radius: 2rem;
            }

            & .title {
                font-size: 2rem;

                & svg {
                    width: 2rem;
                    height: 2rem;
                }
            }

            & .description {
                font-size: 1rem;
            }
        }

    }
}