@import './variables';
@import './components/app-bar';
@import './components/navigation';
@import './components/hero';
@import './components/card';

html {
    scroll-behavior: smooth;
}

body {
    & .splash {
        width: 100vw;
        height: 100vh;
        position: fixed;
        background-color: #fefefe;
        z-index: 9999999999;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    background-color: #fefefe;
}

footer {
    height: 4rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

#spinner:not([hidden]) {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

#spinner::after {
    content: "";
    width: 80px;
    height: 80px;
    border: 2px solid #f3f3f3;
    border-top: 3px solid #f25a41;
    border-radius: 100%;
    will-change: transform;
    animation: spin 1s infinite linear
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

#bg-image {
    position: fixed;
    width: 100%;
    z-index: -9999;
    opacity: .1;
    filter: blur(.5rem) brightness(1.3);
}

.container {
    padding: 2rem;

    & .mt-100 {
        margin-top: 100px;
        margin-bottom: 1rem;
    }

    & .content {
        align-items: center;
        display: flex;
        text-align: center;
        flex-wrap: wrap;
        flex-direction: column;
        text-align: center;
        margin-bottom: 2rem;

        &.details {
            margin-top: 100px;
            flex-direction: row;
            align-items: start;

            #description {
                margin: 0 1rem;
            }
        }

        & p {
            max-width: 742px;
        }

        & .detail-image {
            display: flex;
            flex-direction: column;
            gap: .75rem;

            & .rating {
                position: absolute;
                color: $primary;
                font-weight: bold;
                font-size: 1rem;
                margin: 1.5rem 1rem;

                & .icon svg {
                    width: .7rem;
                    height: .7rem;
                    color: $primary;
                }
            }

            img {
                z-index: 0;
                border-radius: 1rem;
                width: 100%;
                filter: brightness(.9);
            }
        }

    }

    & .grid {
        max-width: 1200px;
        margin: 0 auto;
        display: grid;
        gap: 1rem;
    }
}

.content-detail {
    display: flex;

    #reviwer {
        .review {
            margin-left: 2rem;

            .comment {
                margin-top: 1rem;
            }
        }
    }

    #detail {
        display: flex;
        justify-content: center;
        flex-direction: column;
        flex-wrap: wrap;
        gap: 1rem;
        width: 25rem;

        #drinks ul li,
        #foods ul li {
            list-style: decimal;
            margin-left: 1.7rem;
        }
    }
}

.text-primary {
    color: $primary !important;
}

.text-mute {
    color: #747474;
}

.btn {
    padding: .75rem 1.25rem;
    border-radius: 2rem;
    text-align: center;
    border: none;
    cursor: pointer;

    &.btn-outline-primary {
        background-color: white;
        border: solid .15rem $primary;
        color: $primary;
        font-weight: bold;
        transition: all .2s;

        &:hover,
        &.active {
            background-color: $primary;
            border: solid .15rem $primary;
            color: white;

        }
    }

    &.btn-primary {
        background-color: $primary;
        color: white;

        &:hover {
            color: white;
            background-color: rgba($primary, $alpha: 0.8);
        }
    }
}

.contact {
    display: flex;
    padding-top: 2rem;
    width: 100%;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .wrapper {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: .5rem;
        text-align: center;

        & input,
        & textarea {
            width: 100%;
            border: solid 1px #ddd;
            background-color: #fcfcfc;
        }

        & textarea {
            padding: 0 1rem;
        }

        & input {
            padding: 0.69rem 0;

            &::placeholder {
                padding: 1rem;
            }
        }

        & a {
            border-radius: 3rem;
        }
    }
}

form textarea {
    margin-top: 1rem;
}

.skip-content {
    background-color: white;
    position: absolute;
    top: 90px;
    left: 0;
    padding: .3rem 1rem;
    border-radius: 0 1rem 1rem 0;
    transform: translateX(-100%);
    transition: transform .2s ease-in;
}

:focus-visible.skip-content,
:focus-within.skip-content,
:focus.skip-content {
    transform: translateX(0%);
}


@media only screen and (min-width:520.99px) {
    .contact {
        display: flex;
        padding-top: 2rem;
        width: 100%;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        & .detail-image {
            img {
                width: 403px;
            }
        }

        .wrapper {
            width: 50%;
            display: flex;
            flex-direction: column;
            gap: .5rem;
            text-align: center;

            & input,
            & textarea {
                width: 100%;
                border: solid 1px #ddd;
                background-color: #fcfcfc;
            }

            & textarea {
                padding: 0 1rem;
                width: 100%;
            }

            & input {
                padding: 0.69rem 0;

                &::placeholder {
                    padding: 1rem;
                }
            }

            & a {
                border-radius: 3rem;
            }
        }
    }
}

@media only screen and (max-width:1210px) {
    .container {
        .content {
            &.details {
                justify-content: center;
            }
        }

        .content-detail {
            display: flex;
            flex-wrap: wrap;

            & .box {
                margin-top: 1rem;
            }

            #reviwer {
                .review {
                    margin-left: 2rem;
                }
            }
        }
    }

}

@media only screen and (min-width:1080px) {
    .contact {
        display: flex;
        padding-top: 2rem;
        width: 100%;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .wrapper {
            width: 35%;
            display: flex;
            flex-direction: column;
            gap: .5rem;
            text-align: center;

            & input,
            & textarea {
                width: 100%;
                border: solid 1px #ddd;
                background-color: #fcfcfc;
            }

            & textarea {
                padding: 0 1rem;
                width: 100%;
            }

            & input {
                padding: 0.69rem 0;

                &::placeholder {
                    padding: 1rem;
                }
            }

            & a {
                border-radius: 3rem;
            }
        }
    }
}