header {
  width: 100%;
  height: 80px;
  background-color: rgba($color: #000000, $alpha: .5);
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 1rem;
  position: absolute;
  top: 0;
  z-index: 9999999;

  & .logos {
    display: flex;
    align-items: center;
    width: 20%;

    & .logo {
      width: 25%;
    }

    & .title {
      color: white;
      font-size: 1.3rem;
      color: $primary;
    }
  }

  .listMenu svg {
    display: none;
    padding: .5rem .75rem;
    color: white;
    border-radius: .25rem;
    width: 48px;
    height: 48px;

    &:hover {
      background-color: white;
      color: $primary;
    }
  }

  .nav-bar ul {
    display: flex;
    height: 100%;

    & li a {
      display: block;
      color: white;
      font-size: 1rem;
      padding: 10px 20px;
      border-radius: 50px;
      transition: .3s;
      margin: 0 5px;
    }

    & li a:hover,
    & li a.active {
      color: $primary;
      background-color: rgb(250, 250, 250);
    }
  }
}

@media only screen and (max-width:980px) {
  header {
    padding: 0 2rem;
    justify-content: space-between;

    .listMenu svg {
      display: block;
      padding: .5rem .75rem;
    }

    .logos {
      width: 40%;

      & .logo {
        width: 90px;
      }

      & .title {
        color: white;
        font-size: 2rem;
        color: $primary;
      }
    }

    .nav-bar {
      position: absolute;
      left: 0;
      right: 0;
      width: 100vw;
      background-color: rgba($color: #000000, $alpha: .5);
      transition: .2s;
      z-index: 99999999;
      overflow: hidden;
      height: 0;
      opacity: 0;
      top: 100%;

      & ul {
        display: block;
        width: fit-content;
        margin: 2rem;

        & li {
          width: calc(100% + 10vh);
          margin: 1rem 0;
        }
      }

      &.active {
        height: 500px;
        opacity: 1;
      }
    }
  }

}

@media only screen and (max-width:520px) {
  header {
    height: 5rem;

    .logos {
      .title {
        display: none;
      }
    }
  }
}

@media only screen and (min-width:520.99px) {
  header {
    .logos {
      .title {
        display: block;
        font-size: large;
      }
    }
  }
}